import uniGrid from '@dcloudio/uni-ui/lib/uni-grid/uni-grid.vue'
export default {
	components: {uniGrid},
	methods:{
		bindClick(e){
			switch (e.index) {
				case 0: 
					uni.navigateTo({
						url:"/pages/study/video/video-list"
					})
					break;
				case 1: 
					uni.navigateTo({
						url:"/pages/study/myscore/myscore"
					})
					break;
				case 2: 
					uni.navigateTo({
						url:"/pages/study/report/myScoreReport"
					})
					break;
				case 3: 
					uni.navigateTo({
						url:"/pages/study/comment/myComment"
					})
					break;
				case 4: 
					console.log("1")
					break;
			}
				
		}
	}
}
